/**
 * @file Simplified Operations Schemas - for testing
 * @description Simplified schema exports without complex table dependencies
 */

import { z } from 'zod';

// Simplified schemas for testing without complex imports
export const insertDemandSchema = z.object({
  submitterId: z.string().uuid(),
  type: z.enum(['maintenance', 'complaint', 'information', 'other']),
  assignationResidenceId: z.string().uuid().optional(),
  assignationBuildingId: z.string().uuid().optional(),
  description: z
    .string()
    .min(10, 'Description must be at least 10 characters')
    .max(2000, 'Description must not exceed 2000 characters'),
  filePath: z.string().optional(),
  fileName: z.string().optional(),
  fileSize: z.number().int().optional(),
  residenceId: z.string().uuid().optional(),
  buildingId: z.string().uuid().optional(),
  status: z.string().default('submitted'),
  reviewNotes: z.string().optional(),
});

export const insertDemandCommentSchema = z.object({
  demandId: z.string().uuid(),
  commenterId: z.string().uuid(),
  commentText: z
    .string()
    .min(1, 'Comment content is required')
    .max(1000, 'Comment must not exceed 1000 characters'),
  commentType: z.string().optional(),
  isInternal: z.boolean().default(false),
});

export const demandTypeEnum = z.enum(['maintenance', 'complaint', 'information', 'other']);
export const demandStatusEnum = z.enum([
  'draft',
  'submitted',
  'under_review',
  'approved',
  'in_progress',
  'completed',
  'rejected',
  'cancelled',
]);

export type InsertDemand = z.infer<typeof insertDemandSchema>;
export type InsertDemandComment = z.infer<typeof insertDemandCommentSchema>;